.resume{
    height: 100%;
}
.resume-container{
    display: flex;
    flex-direction: column;
    gap:20px;

}

.resume-display-div.hidden{
    display: none;
}

.resume-button-container{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
    gap: 15px;
}

.resume-frame{
    width: 350px;
    height: 600px;
}

@media screen and (min-width: 500px){
    .resume-frame{
    width: 450px;
    height: 480px;
    }
}

@media screen and (min-width: 650px){
    .resume-frame{
        width: 640px;
    height: 480px;
    }
}

@media screen and (min-height: 650px){
    .resume-frame{
        width: 640px;
    height: 700px;
    }
}

@media screen and (min-width: 900px){
    .resume-frame{
        width: 900px;
    }
}


